import React from "react";
import PropTypes from "prop-types";
import "./movie.css" ;
function Movie ({title,summary,poster,rating,genres,url,}) {
  return(

<div className="movie">
<figure>
    <div className="scale"><a href={url} ><img src={poster} className="poster" alt="profile"/></a></div>
  <div className="movies-data">

    <h3 className="movies-title">{title}</h3>
      <span className="summary">{summary}</span>
  <div className="star">
    <p className=" movies-rating">{rating}/10</p>
    <ul className="genres">{genres.map(genre =>(
      <li className="genres-genre">{genre}</li>
    ))}
    </ul>
  </div>
  </div>
  </figure>
</div>
);
}

Movie.propTypes = {
  id : PropTypes.number.isRequired,
  year : PropTypes.number.isRequired,
  title : PropTypes.string.isRequired,
  summary : PropTypes.string.isRequired,
  poster : PropTypes.string.isRequired,
  genres : PropTypes.arrayOf(PropTypes.string).isRequired

};



export default Movie;
